import { Inline } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Image from '../models';

export default {
  name: 'image',

  match(node: JsonML): boolean {
    return getTagName(node) === 'img';
  },

  convert(node: JsonML): Inline {
    return Image.create({
      data: getAttributes(node) || {},
    });
  },
};
